
import Component from "vue-class-component";
import Vue from "vue";
import firebase from "firebase/app";
import "firebase/auth";
import Loading from "../components/loading/Loading.vue";
import { ruleEmail } from "@/helpers";

@Component({
  components: {
    Loading,
  },
})
export default class LoginPage extends Vue {
  public email: string = "";
  public password: string = "";
  public error: string = "";
  public errorEmail: string = "";
  public errorPassword: string = "";
  public isVisible: boolean = false;
  public loading: boolean = false;
  public handleToggle() {
    this.isVisible = !this.isVisible;
  }
  public signIn() {
    if (!ruleEmail(this.email)) {
      this.errorEmail = "Invalid email!!!";
    } else {
      this.errorEmail = "";
    }
    if (this.password.length < 6) {
      this.errorPassword = "Password of at least 6 characters!!!";
    } else {
      this.errorPassword = "";
    }
    if (ruleEmail(this.email) && this.password.length >= 6) {
      this.loading = true;
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(() => {
          this.loading = false;
          this.$router.push({ name: "Home" });
        })
        .catch((err) => {
          this.loading = false;
          this.error = "Incorrect email or password!!!";
          console.log(err.message);
        });
    }
  }
  public handleRegister() {
    this.$router.push({ name: "Register" });
  }
  public goHome() {
    this.$router.push({ name: "Home" });
  }
}
